own=\"toggle\"] {\r\n  /* padding-top: 4px; */\r\n  background: none;\r\n  border-radius: 6px;\r\n  border: none;\r\n  padding: 5px 6px 2px 6px;\r\n}\r\nbutton[data-dropdown=\"toggle\"]:hover,\r\nbutton[data-dropdown=\"toggle\"]:active {\r\n  background: rgba(0, 103, 181, 0.2);\r\n}\r\npath {\r\n  fill: var(--color-verydarkblue);\r\n  stroke: var(--color-verydarkblue);\r\n}\r\ndiv.dropdown {\r\n  margin-right: 12px;\r\n  text-align: left;\r\n}\r\n.dropdown-list {\r\n  position: absolute;\r\n  z-index: 10;\r\n  gap: 12px;\r\n}\r\n.dropdown-item {\r\n  display: flex;\r\n  gap: 8px;\r\n  align-items: center;\r\n  text-transform: uppercase;\r\n  font-family: var(--lexend);\r\n}\r\ninput#search {\r\n  height: 2rem;\r\n  border-radius: 6px;\r\n  border: 1px solid var(--color-blue);\r\n  padding-left: 5px;\r\n  outline-offset: 0px;\r\n}\r\n\r\n.header-top-line {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n}\r\n.header-bottom-line {\r\n  display: grid;\r\n  grid-template-columns: auto 1fr auto 1fr;\r\n  row-gap: 12px;\r\n  column-gap: 6px;\r\n  font-size: 1.5rem;\r\n  align-items: center;\r\n}\r\n.header-bottom-line .data[data-id=\"date\"] {\r\n  grid-column: span 3;\r\n}\r\n.header-bottom-line img.card-icon {\r\n  width: 24px;\r\n}\r\n\r\n/* Card styles */\r\n.main-content {\r\n  display: flex;\r\n  gap: 12px;\r\n  flex-wrap: wrap;\r\n  align-content: center;\r\n  justify-content: center;\r\n  padding-top: 15px;\r\n  letter-spacing: 1px;\r\n}\r\n@keyframes fadeIn {\r\n  from {\r\n    opacity: 0.2;\r\n  }\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n.card-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 15px;\r\n  background-color: var(--color-white);\r\n  height: max-content;\r\n  width: 350px;\r\n  border-radius: 12px;\r\n  padding: 15px 15px 15px 15px;\r\n  border-top: 6px solid var(--color-blue);\r\n  box-shadow: 2px 2px 3px var(--color-green-brown);\r\n  animation: fadeIn 400ms ease-in forwards; /* The animation is triggered when card-container is created. */\r\n}\r\na {\r\n  text-decoration: none;\r\n}\r\nh2 {\r\n  margin: 0;\r\n}\r\n.header-line {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  width: 310px;\r\n}\r\n.weather-icon {\r\n  width: 60px;\r\n  height: 60px;\r\n}\r\n.card-icon {\r\n  width: 25px;\r\n  height: 25px;\r\n  filter: invert(11%) sepia(21%) saturate(4756%) hue-rotate(196deg)\r\n    brightness(96%) contrast(111%);\r\n  /* Used css filter generator to imitate page title color */\r\n}\r\n.temp-container,\r\n.info-line,\r\n.info-container {\r\n  display: flex;\r\n  gap: 8px;\r\n  align-items: center;\r\n  flex-wrap: wrap;\r\n}\r\n.info-line .data[data-id=\"high\"] {\r\n  padding-left: 33px;\r\n}\r\n\r\n.info-line.wind {\r\n  display: grid;\r\n  grid-template-columns: auto 207px auto;\r\n}\r\n.hourly-btn {\r\n  justify-self: right;\r\n  width: 70px;\r\n  height: 26px;\r\n  border-radius: 8px;\r\n  letter-spacing: 1px;\r\n  background: #ccc;\r\n  background: rgba(0, 103, 181, 0.15);\r\n  border-radius: 6px;\r\n  border: 1px solid var(--color-blue);\r\n  color: inherit;\r\n  font-weight: 600;\r\n}\r\n.hourly-btn:hover {\r\n  background: rgba(0, 103, 181, 0.3);\r\n}\r\n.hourly-btn:active {\r\n  background: rgba(0, 103, 181, 0.4);\r\n}\r\n.data[data-id=\"wind\"] {\r\n  justify-self: left;\r\n}\r\n.info-container {\r\n  gap: 12px;\r\n}\r\n.temp {\r\n  font-size: 2rem;\r\n}\r\n.conditions {\r\n  font-size: 1.2rem;\r\n}\r\n\r\n/* Dropdown */\r\nul.dropdown-list.visible {\r\n  margin-top: 2px;\r\n  margin-bottom: 2px;\r\n}\r\n.dropdown {\r\n  user-select: none;\r\n}\r\n\r\n/* Toggle switch styles */\r\n/* The switch - the box around the slider */\r\n.switch {\r\n  position: relative;\r\n  display: inline-block;\r\n  width: 66px;\r\n  height: 34px;\r\n}\r\n\r\n/* Hide default HTML checkbox */\r\n.switch input {\r\n  opacity: 0;\r\n  width: 0;\r\n  height: 0;\r\n}\r\n\r\n/* The slider */\r\n.slider {\r\n  position: absolute;\r\n  cursor: pointer;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background-color: var(--color-verydarkblue);\r\n  -webkit-transition: 0.4s;\r\n  transition: 0.4s;\r\n}\r\n\r\n.slider:before {\r\n  position: absolute;\r\n  content: \"\";\r\n  height: 26px;\r\n  width: 26px;\r\n  left: 4px;\r\n  bottom: 4px;\r\n  background-color: white;\r\n  -webkit-transition: 0.4s;\r\n  transition: 0.4s;\r\n}\r\n\r\ninput:checked + .slider {\r\n  background-color: rgb(233, 190, 120);\r\n}\r\n\r\ninput:focus + .slider {\r\n  box-shadow: 0 0 1px rgb(233, 190, 120);\r\n}\r\n\r\ninput:checked + .slider:before {\r\n  -webkit-transform: translateX(32px);\r\n  -ms-transform: translateX(32px);\r\n  transform: translateX(32px);\r\n}\r\n\r\n/* Rounded sliders */\r\n.slider.round {\r\n  border-radius: 34px;\r\n}\r\n\r\n.slider.round:before {\r\n  border-radius: 50%;\r\n}\r\n\r\n/* Hourly styles */\r\n.hourly-container {\r\n  display: none;\r\n}\r\n.hourly-container.visible {\r\n  justify-content: center;\r\n  display: flex;\r\n  gap: 20px;\r\n  background-color: var(--color-white);\r\n  height: max-content;\r\n  min-width: 190px;\r\n  max-width: 330px;\r\n  border-radius: 12px;\r\n  padding: 15px 0;\r\n  border-top: 6px solid var(--color-blue);\r\n  box-shadow: 2px 2px 3px var(--color-green-brown);\r\n  animation: fadeIn 400ms ease-in forwards;\r\n  margin-left: 16px;\r\n}\r\n.hourly-container .chevron {\r\n  border: none;\r\n  background: var(--color-blue);\r\n  opacity: 0.2;\r\n  width: 60px;\r\n}\r\n.hourly-container .chevron:hover {\r\n  opacity: 1;\r\n  transition: 300ms;\r\n}\r\n.chevron[data-id=\"left\"] {\r\n  border-radius: 0 50% 50% 0;\r\n}\r\n.chevron[data-id=\"right\"] {\r\n  border-radius: 50% 0 0 50%;\r\n}\r\n.hour-card-list {\r\n  display: flex;\r\n  gap: 20px;\r\n  overflow-x: scroll;\r\n}\r\n.hour-card-container {\r\n  display: grid;\r\n  grid-template-columns: auto 1fr;\r\n  justify-items: center;\r\n  padding-top: 10px;\r\n  row-gap: 4px;\r\n  animation: fadeIn 400ms ease-in forwards;\r\n}\r\n/* .hour-card-container.visible {\r\n  display: grid;\r\n  grid-template-columns: auto 1fr;\r\n  justify-items: center;\r\n  padding-top: 10px;\r\n  row-gap: 4px;\r\n  animation: fadeIn 400ms ease-in forwards;\r\n} */\r\n.hour-card-container [data-id=\"time\"],\r\n.hour-card-container [data-id=\"temp\"],\r\n.hour-card-container .weather-icon {\r\n  grid-column: span 2;\r\n}\r\n.hour-card-container [data-id=\"time\"] {\r\n  font-weight: bold;\r\n}\r\n"],"sourceRoot":""}]);
